import { Link } from "gatsby";
import React from "react";
import * as styles from "../styles/footer.module.css";
import logo from "../images/Chefs Choice Logo.png";


export default function Footer(props) {

    const [email, setEmail] = React.useState('');


  return (
    <div className={styles.Footer} id="footer">

        <img src={logo} alt="logo" className={styles.logo}/>
    
        <form className={styles.newsletter}>
            <h3 className={styles.heading}>{true ? 'Sign up to our newsletter to stay up-to-date with our products and news!' : 'Ürünlerimizden ve gelişmelerden haberdar olmak için bültenimize abone olun!'}</h3>
            <input className={styles.email} type='text' name='email' placeholder={true ? 'Enter your email...' : 'Emailinizi yazin...'} value={email} onInput={(e) => setEmail(e.target.value)} autoComplete="off" />
            <input type="submit" value={true ? 'SIGN UP' : 'ABONE OL'} className={styles.signup}/>
        </form> 

        <div className={styles.subFooter}>
            <Link to="/privacypolicy" className={styles.link}><p>PRIVACY POLICY</p></Link>
            <Link to="/termsofuse" className={styles.link}><p>TERMS OF USE</p></Link>
            <p className={styles.subFooterText}>ATLASJET HAVACILIK SAN. LTD. ŞTİ. 2022 © All Rights Reserved.</p>
        </div>

    </div>
  )
}