import { Link } from "gatsby";
import React from "react";
import * as styles from "../styles/nav.module.css";
import logo from "../images/Chefs Choice Logo.png";


export default function Nav(props) {



  return (
    <div className={styles.Nav}>

        <div className={styles.fade} id="fade"></div>

        <div className={styles.navWindow} id="nav">
          <img src={logo} alt="logo" className={styles.logo}/>
          <Link to="/" className={styles.item} onClick={props.handleNavToggle}>HOME</Link>
          <Link to="/products?cat" className={styles.item} onClick={props.handleNavToggle}>FOR CATS</Link>
          <Link to="/products?dog" className={styles.item} onClick={props.handleNavToggle}>FOR DOGS</Link>
          <Link to="/about" className={styles.item} onClick={props.handleNavToggle}>ABOUT</Link>
          <Link to="/contact" className={styles.item} onClick={props.handleNavToggle}>CONTACT US</Link>
        </div>

    </div>
  )
}   